export default {
	data() {
		return {
			barDetail: ''
		}
	},
	methods: {
		openLocation() {
			uni.getLocation({
				type: 'gcj02', //返回可以用于uni.openLocation的经纬度
				success: function (res) {
					const latitude = res.latitude;
					const longitude = res.longitude;
					uni.openLocation({
						latitude: latitude,
						longitude: longitude,
						success: function () {
							console.log('success');
						}
					});
				}
			});
		}
	},
	onLoad(options) {
		// console.log(JSON.parse(options.itemStr));
		this.barDetail = JSON.parse(options.itemStr);
	}
}
